// actions
export const SIGNIN = 'SIGNIN';
export const SIGNUP = 'SIGNUP';
export const LOGOUT = 'LOGOUT';

// actions creators
export function signin(mobileNo, password) {
	return {
		type: SIGNIN,
		mobileNo,
		password
	};
}

export function signup(index) {
	return {
		type: SIGNUP,
		index
	};
}

export function logout(mobileNo) {
	return {
		type: LOGOUT,
		mobileNo
	};
}


